<template>
  <div id="user-login">
    <mt-header title="注册" id="registerHerder">
      <router-link to="/UserLogin" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="registerFrom">

      <!-- 手机号码 -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPhone" v-model="phone" placeholder="请输入手机号" onkeyup="value=value.replace(/[^\d]/g,'') " ng-pattern="/[^a-zA-Z]/" maxlength=11></el-input>
        </el-col>
      </el-row>

      <!-- 支付密码 -->
       <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerUserCode" v-model="zfpsword" placeholder="请输入支付密码" ></el-input>
        </el-col>
      </el-row> 

      <!-- 密码 -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPassword" v-model="password" type="password" placeholder="请输入密码"></el-input>
        </el-col>
      </el-row>

      <!-- 确认密码 -->
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="registerPwd" v-model="urepwd" type="password" placeholder="请确认密码"></el-input>
        </el-col>
      </el-row>

      <h5 class="tbTxt">账户余额体现信息</h5>
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input  id="nameTxt"  type="text" placeholder="请输入真实姓名" v-model="userName" :attr="{ minlength:1,maxlength:4 }"></el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="baxTxt"  type="text" placeholder="请输入银行卡号" v-model="bankCard" :attr="{maxlength:19 }"></el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-input id="baxNameTxt"  v-model="trueName" type="text" placeholder="请输入银行卡名"></el-input>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="18" class="register-el-col">
          <el-button id="registerLogin" @click="UserRegister"  style="width:100%" type="primary">注册</el-button>
        </el-col>
      </el-row>

      
      <router-link to="UserLogin" class="ToLogin"><span>已有账号？马上登录</span></router-link>

    </div>

  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  import axios from 'axios';
  export default {
    name: 'user-login',
    data () {
      return{
        phone:'', //手机号
        password:'',//密码
        urepwd:'',//确认密码
        zfpsword:'', //支付密码

        userName:'',  //银行账户姓名
        bankCard:'',  //银行卡号
        trueName:'', //银行卡名
        nameflag:0,   //属性是否校验通过flag
        //code:'',
        //show: true,
       ///count: '',
        //timer: null
      }
    },
    methods : {
      UserRegister:function(){
        //获取值
        var phone = this.phone,
            password = this.password,
            urepwd = this.urepwd,
            zfpsword = this.zfpsword,
            userName = this.userName,  //银行账户姓名
            bankCard = this.bankCard,  //银行卡号
            trueName = this.trueName, //银行卡名
            //UserCode = this.UserCode,
            reg= /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/;
            this.userName = this.userName.replace(/(^\s+)|(\s+$)/g, "");
        if(phone==''){
          alert("请输入手机号码");
          return false;
        }else if(!reg.test(this.phone)){
          alert("手机格式不正确");
          return false;
        }
        if (zfpsword == ''){
          alert('支付密码不能为空');
          return false;
        }
        if (password == ''){
          alert('登录密码不能为空');
          return false;
        }
        if (password != urepwd) {
          alert('两次登录密码输入不一致');
          return false;
        }
        if(!this.userName.match(/^[\u4e00-\u9fa5]{2,4}$/)){
          this.nameflag=0;
          alert("用户名请输入2-4个汉字")
          // this.tipTxt="用户名请输入2-4个汉字";
          // this.tipBox();
          return false;
        }


        axios.post("/api/user/save.dnl",{
          phone: this.phone, //手机号
          password: this.password, //账户登录密码
          zfpsword: this.zfpsword,  //充值支付密码

          userName:this.userName,  //银行账户姓名
          bankCard:this.bankCard,  //银行卡号
          trueName:this.trueName  //银行卡名

        }).then((data)=>{
          //alert(1)
          console.log(data)
          //console.log(this.phone)
          //console.log(data.data.phone)
          if(data.data.code == 10000){
            alert(data.data.msg)
            location.href = 'UserLogin'  
            return true
          }else{
            //alert(111)
            alert(data.data.msg)
            //location.href = 'UserLogin' 
            return false
          }
        })
      },
      // 短信验证码
      // getCode(){
      //   const time_count= 10;
      //   const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
      //   if(this.phone==''){
      //     alert("请输入手机号码");
      //     return false;
      //   }else if(!reg.test(this.phone)){
      //     alert("手机格式不正确");
      //     return false;
      //   }
      //   if (!this.timer) {
      //     this.count = time_count;
      //     this.show = false;
      //     this.timer = setInterval(() => {
      //       if (this.count > 0 && this.count <= time_count) {
      //         this.count--;
      //       } else {
      //         this.show = true;
      //         clearInterval(this.timer);
      //         this.timer = null;
      //       }
      //     }, 1000)
      //   }
      // },

    }
  }
</script>

<!-- 添加 "scoped" 表示样式只对当前页面生效 -->
<style>

  /* 注册头部样式 */
  #registerHerder{background-color: #FF414F;color: #fff;}

  /* from表单样式 */
  .registerFrom{margin-top: 1rem;}
  .el-input{display: block}
  .register-el-col{float: none;margin: 0 auto 0.3rem auto; position: relative}

  /* input表单样式 */
  #registerPhone,#registerPassword,#registerPwd,#nameTxt,#baxTxt,#baxNameTxt{background: #F7F4F8;border: none;color: #b7b5b5;}
  #registerUserCode{background: #F7F4F8;border: none;color: #b7b5b5;}

  /* 验证码按钮样式 */
  .registerCount{position: absolute;top: 0;height: 100%;right: 0;width: 42%;background: rgba(255,65,79,1);padding: 0 8px;color: #fff;}

   /* 登陆button样式 */
  #registerLogin{background: #FF414F;border-color: #FF414F;}
  .el-button--primary:focus, .el-button--primary:hover {background: #FF414F;border-color: #FF414F;}

  /* 有账号登陆样式 */
  .ToLogin{color: rgba(255,129,126,1);font-size: 0.28rem;text-align: center;display: inline-block;width: 100%;font-weight: 600;}

  .tbTxt{font-size: 0.26rem;text-align: center;margin-bottom: 0.3rem;}
</style>



