<template>
    <div class="ForgetPassword">

      <mt-header title="忘记密码" id="forgetHerder">
        <router-link to="/UserLogin" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>

      <div class="forgectFrom">
        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-input id="ForgetPhone" v-model="phone" placeholder="请输入手机号" class="el-input tico"></el-input>
          </el-col>
         </el-row>

        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-input id="ForgetUserCode" v-model="UserCode" placeholder="请输入验证码" ></el-input>
            <mt-button type="default" size="small" v-show="show" @click="getCode" class="forgetCount">获取验证码</mt-button>
            <mt-button type="primary" size="small" v-show="!show" class="forgetCount">已发送（{{count}} s）</mt-button>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-input id="newPsd" v-model="password" type="password" placeholder="新密码"></el-input>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-input id="repeatNewPsd" v-model="urepwd" type="password" placeholder="确认密码"></el-input>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="18" class="forget-el-col">
            <el-button id="forgetLogin" @click="UserRegister"  style="width:100%" type="primary">确定</el-button>
          </el-col>
        </el-row>
      </div>
    </div>
</template>

<script>
    export default {
        name: "ForgetPassword",
        data () {
          return{
            phone:'', //手机号
            password:'',//密码
            urepwd:'',//确认密码
            UserCode:'',
            code:'',
            show: true,
            count: '',
            timer: null
          }
        },
      methods : {
        UserRegister:function(){
          //获取值
          var phone = this.phone,
            password = this.password,
            urepwd = this.urepwd,
            UserCode = this.UserCode,
            reg= /^1[3|4|5|7|8][0-9]\d{8}$/;
          if(phone==''){
            alert("请输入手机号码");
            return false;
          }else if(!reg.test(this.phone)){
            alert("手机格式不正确");
            return false;
          }
          if (UserCode == ''){
            alert('验证码不能为空');
            return false;
          }
          if (password == ''){
            alert('密码不能为空');
            return false;
          }
          if (password != urepwd) {
            alert('两次密码输入不一致');
            return false;
          }
          //
          alert("修改成功");
          location.href = 'UserLogin'
          //window.location.href='UserLogin'

        },
        // 短信验证码
        getCode(){
          const time_count= 10;
          const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
          if(this.phone==''){
            alert("请输入手机号码");
            return false;
          }else if(!reg.test(this.phone)){
            alert("手机格式不正确");
            return false;
          }
          if (!this.timer) {
            this.count = time_count;
            this.show = false;
            this.timer = setInterval(() => {
              if (this.count > 0 && this.count <= time_count) {
                this.count--;
              } else {
                this.show = true;
                clearInterval(this.timer);
                this.timer = null;
              }
            }, 1000)
          }
        },

      }
    }

</script>

<style>

  /* 头部样式 */
  #forgetHerder{background-color: #FF414F;color: #fff;}

  /* from表单样式 */
  .forgectFrom{margin-top: 1rem;}
  .forget-el-col{float: none;margin: 0 auto 0.3rem auto; position: relative}
  .el-input {display: block;}
  
  /* input文本框样式 */
  #ForgetPhone,#newPsd,#repeatNewPsd{background: #F7F4F8;border: none;color: #b7b5b5;}
  #ForgetUserCode{width: 46%;background: #F7F4F8;border: none;color: #b7b5b5;}

  /* 登陆button样式 */
  #forgetLogin{background: #FF414F;border-color: #FF414F;}
  .el-button--primary:focus, .el-button--primary:hover {background: #FF414F;border-color: #FF414F;}

  /* 验证码按钮样式 */
  .forgetCount{position: absolute;top: 0;height: 100%;right: 0;width: 42%; background: rgba(255,65,79,1) ;padding: 0 8px; color: #fff;}

  
</style>



